@import url("https://fonts.googleapis.com/css2?family=Cardo&family=Lato&family=Noto+Naskh+Arabic:wght@600;700&family=Open+Sans:wght@500&family=PT+Sans&family=PT+Serif&family=Raleway:wght@500&display=swap");

*,
::after,
::before {
  font-family: "PT Serif";
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100%;
  height: 100vh;
}

.wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  backdrop-filter: blur(10px);
}

@media (max-width: 850px) {
  .App {
    height: auto;
  }

  .wrapper {
    height: auto;
    padding: 50px;
  }
}

@media (max-width: 500px) {
  .wrapper {
    padding: 0;
  }
}
